@import "variables.scss";

.advantages {
  padding: 20px 20px 49px;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    align-items: flex-start;
    border-bottom: 1px solid #e6e6e6;
    padding: 0 30px;
    margin-bottom: 47px;
  }

  @media (min-width: $desktop-width) {
    border: none;
    padding: 0;
  }

  &__title {
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
      margin-bottom: 32px;
    }
  }

  &__icon {
    position: relative;
    display: inline-block;
    min-width: 60px;
    min-height: 60px;
    background-color: $background-block-special;
    text-align: center;
    align-content: center;

    @media (min-width: $tablet-width) {
      min-width: 80px;
      min-height: 80px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @media (min-width: $tablet-width) {
      display: grid;
      grid: 1fr 1fr /1fr 1fr;
      row-gap: 37px;
      column-gap: 153px;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
      margin-bottom: 69px;
      gap: 100px;
      padding: 0 84px;
    }
  }

  &__item {
    display: flex;
    gap: 20px;
    align-items: center;

    @media (min-width: $tablet-width) {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      gap: 31px;
      max-width: 269px;
      height: 280px;
      background-repeat: no-repeat;
      background-position-x: right;
    }
  }

  &__text-wrapper {
    font-family: $content-font;
    font-size: $font-small;
    line-height: $line-height-low;
    color: $color-text-primary;

    @media (min-width: $tablet-width) {
      font-size: $font-medium;
      line-height: 24px;
    }
  }

  &__item:nth-child(1) {
    @media (min-width: $tablet-width) {
      background-image: url("../img/1.png");
    }
  }

  &__item:nth-child(2) {
    @media (min-width: $tablet-width) {
      background-image: url("../img/2.png");
    }
  }

  &__item:nth-child(3) {
    @media (min-width: $tablet-width) {
      background-image: url("../img/3.png");
    }
  }

  &__item:nth-child(4) {
    @media (min-width: $tablet-width) {
      background-image: url("../img/4.png");
    }
  }
}
